h2{
  font-size: 26px;
}
h3{
  font-size: 22px;
}
.invisible{
  visibility: hidden;
}
.cat-info{
  display: flex;
  flex-direction: row;
  gap: 50px;
  justify-content: center;
}
.cat-info p{
  width: 600px;
  font-size: 20px;
}
.loader {
  position: relative;
  width: 100px;
  height: 100px;
}
.loader:before,
.loader:after {
  content: '';
  border-radius: 50%;
  position: absolute;
  inset: 0;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.3) inset;
}
.loader:after {
  box-shadow: 0 2px 0 #0004ff inset;
  animation: rotate 2s linear infinite;
}
@keyframes rotate {
  0% {
      transform: rotate(0)
  }
  100% {
      transform: rotate(360deg)
  }
}
.centered {
  position: fixed;
  top: 50%;
  left: 50%;
}
select { 
  background-color: rgb(208, 234, 245);
  border: thin solid blue;
  border-radius: 4px;
  display: inline-block;
  font: inherit;
  line-height: 1.5em;
  padding: 0.5em 3.5em 0.5em 1em;

  margin-bottom: 40px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
select.breed-select {
  background-image:
      linear-gradient(45deg, transparent 50%, blue 50%),
      linear-gradient(135deg, blue 50%, transparent 50%),
      linear-gradient(to right, yellow, yellow  );
  background-position:
      calc(100% - 20px) calc(1em + 2px),
      calc(100% - 15px) calc(1em + 2px),
      100% 0;
  background-size:
      5px 5px,
      5px 5px,
      2.5em 2.5em;
  background-repeat: no-repeat;
}